.greetingForm {
  display: flex;
  align-items: flex-start;
}

.inputAndButtonContainer {
}

.error {
  color: red;
  height: 21px;
  margin-left: 10px;
}

.input {
  margin: 10px;
  background: aquamarine;
  color: #040405;
}

.input:focus {
  outline: none;
  border: aqua solid 2px;
}

.errorInput {
  margin: 10px;
  background: red;
  color: purple;
  border: 2px solid red;
  outline: none;
}

.button {
  margin: 10px;
  width: 60px;

  background: greenyellow;
  color: chocolate;
  outline: none;
}

.button:focus {
  outline: palevioletred solid 1px;
}

.button:active {
    background: gold
}

.button:disabled {
    color: gray;
}

